import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import 'reset-css/reset.css';
import '../color/color';
import '../interactions/interactions';
import '../typography/typography';
import css from './navigation-disclosure.css';

import NavigationDisclosure from './navigation-disclosure';

const NavigationDisclosureIcon = ({ icon, className, ...otherProps }) => (
  <NavigationDisclosure
    className={classnames(className, css['ds-navigation-disclosure--icon'], {
      [css[`ds-navigation-disclosure--icon-${icon}`]]: icon,
    })}
    {...otherProps}
  />
);

NavigationDisclosureIcon.defaultProps = {
  icon: null,
  className: null,
};

NavigationDisclosureIcon.propTypes = {
  className: PropTypes.string,
  icon: PropTypes.oneOf(['account', 'search']),
};

export default NavigationDisclosureIcon;
